@import url(https://fonts.googleapis.com/css?family=Rokkitt);
@font-face {
  font-family: 'Rokkit';
  font-display: auto;
  src: local("Rokkit"), url(/static/media/rokkitt.6cff0ff4.woff2) format("woff2"); }

body * {
  font-family: "Rokkitt", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body * {
  box-sizing: border-box; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

header {
  z-index: 200;
  min-height: 120vh;
  background-color: #3d5afe;
  background-image: url(/static/media/-mountain.4057ac30.webp);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: cover; }
  header .headercontainer {
    height: 90vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
  header .getintouch {
    display: flex;
    justify-content: flex-end; }
  header .headercontent .dan {
    top: 30%;
    left: 0;
    right: 0;
    text-transform: uppercase; }
    header .headercontent .dan h1,
    header .headercontent .dan h2 {
      color: white; }
  header .scrollDown {
    display: flex;
    justify-content: center; }
  header .scrollTop {
    z-index: 2000;
    position: fixed;
    bottom: 30px;
    right: 30px; }
    header .scrollTop img {
      border-radius: 50%;
      background-color: #8c9eff;
      padding: 10px; }

.skills {
  margin-top: -10rem; }
  .skills h2, .skills h3 {
    color: black; }
  .skills .container {
    max-width: 992px; }

footer {
  background-color: #323642;
  color: white; }

.contacts {
  min-height: 100vh;
  border-top: 10px solid #3d5afe;
  margin-bottom: auto;
  background-color: white; }
  .contacts .content {
    max-width: 992px; }

.projects {
  border-top: 10px solid #3d5afe;
  min-height: 100vh; }

